// read a cookie
export function getCookieValue(a) {
    var b = document.cookie.match("(^|;)\\s*" + a + "\\s*=\\s*([^;]+)");
    return b ? b.pop() : "";
}

export function resetCookie(a) {
    document.cookie = `${a}=; expires=Thu, 01 Jan 1970 00:00:00 GMT`;
}

/**
 * alternative: get cookie by name with using a regular expression
 */
export function getCookiebyName(name) {
        let pair = document.cookie.match(new RegExp(name + '=([^;]+)'));
        let domainuser = !!pair ? pair.input.split('=') : undefined;
        let user = Array.isArray(domainuser) ? domainuser[1].split('\\') : undefined;
        return user ? user.join('/') : 'unknown';
};