import { ulid } from "../../ulid";

class message {
    constructor(author, to, text, senttime, readtime, read) {
        this.id = ulid();
        this.author = author;
        this.to = to;
        this.content = text;
        this.senttime = senttime;
        this.readtime = readtime;
        this.read = read;
    }
};

export default message;