import { writable } from 'svelte/store';

// constructor function
const createStore = () => {
    let storeContent = [];
    // initialize internal writable store with empty list
    const { subscribe, set, update } = writable(storeContent);

    // mark message as read by removing it from the list
    const remove = (message) => {
        update(messages => messages.filter(mess => mess.id !== message.id));
    };

    // add new message to the bottom of the list
    const add = (message) => update(messages => [...messages, message]);

    const addarr = (arr) => update(messages => [...messages, ...arr]);

    return {
        subscribe,
        add,
        addarr,
        set,
        remove,
        clear: () => set([])
    };
};

// initialize the store
const chatmessages = createStore();
const users = writable([]);

export { chatmessages, users };