export default function shortenAuthor(author, lastPlayed) {
    let dt = "";
    // TODO aired time should be updated from other
    const d = new Date(lastPlayed[lastPlayed.length - 1]);
    if (d.getTime() === d.getTime()) {
        dt = d.toLocaleTimeString();
    }

    if (typeof author === 'string') {
        const a = author.split("/").length === 2 ? author.split("/")[1] : author.split("/")[0];
        const auth = a === "" ? a : a.split(".").join(" ").replace(/\b\w/g, l => l.toUpperCase());
        const d = dt !== "" ? `, aired: ${dt}` : "";
        return auth !== "" ? `${auth}${d}` : `${d}`;
    }
}