import { ulid } from "./ulid";

function dateNow() {
    const d = new Date();
    return d.toISOString();
}

function createEmptyBNMsg(template, lastArrElem) {
    const t = dateNow();
    return {
        id: ulid(),
        created: t,
        modified: [],
        text: t,
        template: template,
        messages: [
            createEmptyLine(lastArrElem)
        ]
    }
}

/**
 * creates an empty line, setting tags according to the parameter
 *
 * @param {*} arr
 * @returns
 */
function createEmptyLine(arr) {
    let template;
    let tags = [
        { id: "01", label: "on air", value: "on-air", checked: true },
        { id: "02", label: "website", value: "website", checked: true },
        { id: "03", label: "Twitter", value: "twitter", checked: true }
    ]
    if (Array.isArray(arr) && arr.length === 1) {
        tags = [
            { id: "01", label: "on air", value: "on-air", checked: arr[0]["tags"].find(tag => tag.value === "on-air")["checked"] },
            { id: "02", label: "website", value: "website", checked: arr[0]["tags"].find(tag => tag.value === "website")["checked"] },
            { id: "03", label: "Twitter", value: "twitter", checked: arr[0]["tags"].find(tag => tag.value === "twitter")["checked"] }
        ];

        template = arr[0].template;
    }
    return {
        id: ulid(),
        text: "",
        template: template,
        aired: [],
        dirty: false,
        deleted: false,
        protected: false,
        selected: false,
        tags: tags
    }
}

export { createEmptyBNMsg, createEmptyLine }