import axios from "axios";
import urlJoin from "proper-url-join";
import { CouchDbSaveError, CouchDbError } from "./errors";
import deepClone from "./deepclone";

export default class CouchDb {
    constructor(host, user, pass, db) {
        this.host = host;
        this.db = db;
        this.axios = axios.create({
            baseUrl: host,
            timeout: 15000,
            auth: {
                username: user,
                password: pass
            },
            headers: { "Accept": "application/json", "Content-Type": "application/json" }
        });
    }

    Health() {
        this.axios.get(urlJoin(this.host, "up"))
            .then(function (response) {
                console.log("-----------------");
                console.log(response.data);
                console.log("-----------------");
            })
            .catch(error => console.log("health: couchdb timeout"));
    }

    async GetData() {
        try {
            const response = await this.axios.post(urlJoin(this.host, this.db, "_find"), {
                selector: {
                    _id: {
                        $gt: null
                    }
                },
                "limit": 200,
                sort: [{ created: "desc" }]
            });
            const data = await response.data;
            return data.docs;
        } catch (error) {
            throw new CouchDbError(error.message);
        }
    }

    async getDoc(id) {
        try {
            const response = await this.axios.get(urlJoin(this.host, this.db, id));
            const data = await response.data;
            return data;
        } catch (error) {
            throw new CouchDbError(error); 
        }
    }

    async SaveDoc(doc) {
        try {
            const t0 = performance.now();
            const cleanDoc = removeGuiProperties(deepClone(doc));
            const response = await this.axios.put(urlJoin(this.host, this.db, cleanDoc.id), cleanDoc);
            const data = await response.data;
            doc["_rev"] = data["rev"];
            if (!doc.hasOwnProperty("_id")) {
                doc["_id"] = data["id"];
            }
            const t1 = performance.now();
            console.log(`Call to SaveDoc() took ${t1 - t0} milliseconds.`);
            return data;
        } catch (error) {
            console.log("error while saving to CouchDb: ", error);
            throw new CouchDbSaveError(error);
        }
    }

    
    /**
     * GetTemplates retrieves template's definition
     *
     * @returns array of template objects - hard limit 24
     * @memberof CouchDb
     */
    async GetTemplates() {
        try {
            const response = await this.axios.get(urlJoin(this.host, this.db, "templates"), {
                "limit": 24,
            });
            const data = await response.data;
            return data.templates;
        } catch (error) {
            throw new CouchDbSaveError(error.message);
        }
    }
}

function removeGuiProperties(doc) {
    doc.messages.forEach(l => { delete l.selected; });
    return doc;
}
