import * as xmldom from "xmldom";
import { PositionType, SnapshotType } from "../../enums";
import { PositionTypeError, SnapshotTypeError } from "../../errors";

const snaphost = `<snapshotrequest xmlns="http://www.vizrt.com/snapshotrequest">
    <position>
        <namedposition>$pilot1</namedposition>
    </position>
    <videomoderequest>
        <width>1920</width>
        <height>1080</height>
    </videomoderequest>
    <snapshotdata view="all" />
    <context>
        <entry key="concept" value="NEWS" />
        <entry key="variant" value="Default" />
    </context>
</snapshotrequest >`;

/**
 * Builds a "snapshot" xml fragment for Vizrt preview server url
 *
 * @param {*} sd
 * @returns
 */
function buildSnapshot(sd) {
    let xmlDoc = new xmldom.DOMImplementation().createDocument("", "", null);
    let snapshotrequest = xmlDoc.createElementNS("http://www.vizrt.com/snapshotrequest", "snapshotrequest");
    snapshotrequest.appendChild(AddPosition(xmlDoc, sd.StopPointType, sd.StopPointValue));
    snapshotrequest.appendChild(AddVideoMode(xmlDoc, sd.Width, sd.Height));
    snapshotrequest.appendChild(AddSnapshotData(xmlDoc, sd.SnapshotType));
    snapshotrequest.appendChild(AddConceptVariant(xmlDoc, { concept: sd.Context.concept, variant: sd.Context.variant }));

    return snapshotrequest;
}

function createXmlDoc(root) {
    return builder.fragment().ele(root);
}

function AddAttr(doc, attArr) {
    attArr.forEach(element => {
        doc.att(element["name"], element["value"]);
    });
    return doc;
}

function AddPosition(doc, type, val) {
    const pos = doc.createElement("position");
    const value = doc.createTextNode(val);
    let namedpos;
    switch (type) {
        case PositionType.namedposition:
            namedpos = doc.createElement(PositionType.namedposition.value);
            break;
        case PositionType.fposition:
            namedpos = doc.createElement(PositionType.fposition.value);
            break;
        case PositionType.absoluteposition:
            namedpos = doc.createElement(PositionType.absoluteposition.valuee);
            break;
        default:
            throw new PositionTypeError("unknown position description " + JSON.stringify(type));
    }
    namedpos.appendChild(value);
    pos.appendChild(namedpos);

    return pos;
}

function AddVideoMode(doc, width, height) {
    const videomoderequest = doc.createElement("videomoderequest");

    const wdth = doc.createElement("width");
    const w = doc.createTextNode(width);

    const hght = doc.createElement("height");
    const h = doc.createTextNode(height);
    videomoderequest.appendChild(wdth).appendChild(w);
    videomoderequest.appendChild(hght).appendChild(h);

    return videomoderequest;
}

function AddSnapshotData(doc, type) {
    const snapshotdata = doc.createElement("snapshotdata");
    switch (type) {
        case SnapshotType.all:
            snapshotdata.setAttribute("view", SnapshotType.all.value);
            break;
        case SnapshotType.key:
            snapshotdata.setAttribute("view", SnapshotType.key.value);
            break;
        case SnapshotType.fill:
            snapshotdata.setAttribute("view", SnapshotType.fill.value);
            break;
        default:
            throw new SnapshotTypeError("unknown snapshotdata value: " + JSON.stringify(type));
    }
    return snapshotdata;
}

function AddConceptVariant(doc, cvObj) {
    const cv = doc.createElement("context");
    const concept = doc.createElement("entry");
    const variant = doc.createElement("entry");

    concept.setAttribute("key", "concept");
    concept.setAttribute("value", cvObj["concept"]);

    variant.setAttribute("key", "variant");
    variant.setAttribute("value", cvObj["variant"]);

    cv.appendChild(concept);
    cv.appendChild(variant);

    return cv;
}

export { buildSnapshot, createXmlDoc, AddPosition, AddVideoMode, AddAttr, AddSnapshotData, AddConceptVariant };