<script>
  import { getContext } from "svelte";
  import HelpPoput from "./Helppopup.svelte";
  import Dialog from "./Dialog.svelte";
  import { createEventDispatcher } from "svelte";
  import { fade, fly } from "svelte/transition";

  const dispatch = createEventDispatcher();

  let marked = false;
  export let duration;

  export const toggle = () => marked = !marked
 
  const { open } = getContext("simple-modal");
  const showPopup = () => {
    open(
      HelpPoput,
      { message: "Breaking News help page" },
      {
        transitionBgProps: { duration: 50 },
        transitionWindow: fly,
        transitionWindowProps: {
          y: 100,
          duration: 250,
        },
      }
    );
  };

  const showDialog = () => {
		open(
			Dialog,
			{
        message: "Auto playout",
        value: duration,
				hasForm: true,
				onCancel,
				onOkay
      },
      {
        transitionBgProps: { duration: 100 },
        transitionWindow: fade,
        transitionWindowProps: {
          y: 100,
          duration: 100,
        },
      },
			{
				closeButton: false,
    		closeOnEsc: false,
    		closeOnOuterClick: false,
			}
	  );
  };
  
  const onCancel = (text) => {
    marked = false;
		status = -1;
	}
	
	const onOkay = (text) => {
    status = 1;
    marked = false;
    dispatch("okay", {
        duration: text,
        loop: 1
      });
  }
  
  $: marked ? showDialog() : "";  

</script>

