export const notifications = {
    SimultaneousEditing : function(author) { return `<span><strong>${author}</strong> has just edited the story you are working on, please contact him</span>` },
    PlayError : function(err) { return `<span><strong>Playout: </strong>${err}</span>`},
    StopError: function (err) { return `<span><strong>Take out:</strong> ${err}</span>`; },
    TwitterError: function (err) { return `<span><strong>Twitter:</strong> ${err}</span>`; },
    WebsiteError: function (err) { return `<span><strong>Website:</strong> ${err}</span>`; }
}