class AudioTimer {

    constructor() {
        this.audiocontext = new (window.AudioContext || window.webkitAudioContext)();
        this.silentOscillator = null;
    }

    // static audiocontext;

    /**
     * Executes a callback after a delay. Uses WebAudio 
     * and a silent oscillator to achieve the precise scheduling
     *
     * @param {*} delay
     * @param {*} callbackFunction
     */
    Cue(duration) {
        var self = this;
        return new Promise(function (resolve, reject) {
            if (self.audiocontext !== undefined) {
                const volume = self.audiocontext.createGain();
                volume.gain.value = 0;
                volume.connect(self.audiocontext.destination);

                self.silentOscillator = self.audiocontext.createOscillator();
                self.silentOscillator.connect(volume);

                self.silentOscillator.onended = () => {
                    resolve(true);
                };

                self.silentOscillator.start(self.audiocontext.currentTime);
                self.silentOscillator.stop(self.audiocontext.currentTime + duration);
            } else {
                reject("Sorry, but your browser doesn't support the Web Audio API");
            }
        });
    };

    Stop() {
        this.silentOscillator.stop();
        this.silentOscillator = null;
    }
}

export { AudioTimer };
