import * as xmldom from "xmldom";

// const p = `<payload xmlns="http://www.vizrt.com/types" model="http://localhost:8177/templates/10002/model/NEWS/Default">
//     <field name="Header">
//         <value>1111 1111 1111 1111</value>
//     </field>
//     <field name="ALine1">
//         <value>222 2222 2222 22222</value>
//     </field>
// </payload>`

function buildPayload(externalId, model, mappings, data) {
    let xmlDoc = new xmldom.DOMImplementation().createDocument("", "", null);
    
    let payload = xmlDoc.createElementNS("http://www.vizrt.com/types", "payload");
    payload.setAttribute("model", model);
    xmlDoc.appendChild(payload);
  
    mappings.fields.forEach((item, idx) => {
        const value = isValidIndex(idx, data) ? data[idx] : "";
        payload.appendChild(AddFieldNode(xmlDoc, item, value));
    });
    return xmlDoc;
}

function AddFieldNode(xmlDoc, key, value) {
    const field = xmlDoc.createElement("field");
    field.setAttribute("name", key);
    
    const val = xmlDoc.createElement("value");
    const v = xmlDoc.createTextNode(value);
    val.appendChild(v);
    
    field.appendChild(val);
    xmlDoc.appendChild(field);

    return field;
}

function isValidIndex(idx, data) {
    return (idx >= 0 && idx < data.length);
}

export { buildPayload, isValidIndex };