import { format } from "date-fns";
import { LogLevel } from "./enums";
import { ulid } from "./ulid";

export class LogMessage {
    constructor(msg, lvl) {
        this.id = ulid();
        this.message = msg;
        this.level = lvl;
    }

    get FormattedLogMessage() {
        let lvl;
        let color;
        switch (this.level) {
            case LogLevel.Info:
                lvl = "INFO";
                color = "grey";
                break;
            case LogLevel.Warn:
                lvl = "WARNING";
                color = "orange";
                break;
            case LogLevel.Error:
                lvl = "ERROR";
                color = "darkred";
                break;
            case LogLevel.Fatal:
                lvl = "FATAL";
                color = "red";
                break;
            default:
                lvl = "UNKNOWN";
                color = "black";
                break;
        }
        return `<span style='display: none'>${this.id}</span><span style='color: grey;'>${this.dateNow}</span> <span style='color: ${color};'>${lvl}:</span><br/><p style='margin-top: 0.3em;'>${this.message}</p>`;
    }

    get dateNow() {
        const d = new Date();
        return format(d, 'dd/MM/yyyy H:mm:ss');
    }
}
