/**
 * Filter breaking news data tree for search purposes *
 * @param {array} data
 * @param {string} string
 * @param {string} field
 * @returns
 */
export function filterJson(data, string, field) {
    if (string.length < 2) {
        return data;
    }
    else {
        // string = string.toLowerCase();
        const arr = [];
        for (let i = 0; i < data.length; i++) {
            if (typeof (data[i]) == 'object' && data[i] !== null) {
                if (data[i][field].indexOf(string) >= 0) {
                    arr.push(data[i]);
                }
                else {
                    let messages = data[i].messages;
                    for (let j = 0; j < messages.length; j++) {
                        if (messages[j].text.indexOf(string) >= 0) {
                            arr.push(data[i]);
                            continue;
                        }
                    }
                }
            }
        }
        return arr;
    }
}