// https://www.andrewnater.com/posts/enums-in-javascript/

const freeze = obj => Object.freeze(obj);

function enumeration(...members) {
    let memberValues = {};
    for (const member of members) {
        // we're only supporting string or object arguments
        if (typeof member === "string") {
            memberValues[member] = freeze({ value: member });
        } else if (typeof member === "object") {
            // use the first provided key to support { key: value } usage
            const key = Object.keys(member)[0];
            memberValues[key] = freeze({ value: member[key] });
        }
    }
    return freeze(memberValues);
}

const PositionType = enumeration(
    "namedposition",
    "fposition",
    "absoluteposition"
);

const SnapshotType = enumeration(
    "all",
    "fill",
    "key"
);

const LogLevel = enumeration(
    "Info",
    "Warn",
    "Error",
    "Fatal"
);

const UserState = enumeration(
    { "Active": "active" },
    { "Idle": "idle" },
    { "Inactive": "inactive" }
);

export { PositionType, SnapshotType, LogLevel, UserState }


